import React, { useState, Fragment } from "react";
import path from "path";
import Particles from "react-particles-js";
import { CircularProgress, TextField, Button } from "@material-ui/core";
import { Image } from "semantic-ui-react";
import "./style.css";
import { Result } from "antd";
import url from "../../../strings/url";
class RP extends React.PureComponent {
  state = { loading: true, status: "", password: "", repass: "" };
  componentDidMount() {
    this.setState({ loading: true });
    const headers = new Headers();
    headers.append("content-type", "application/json");
    console.log(this.props.match.params.token);
    fetch(`${url}/verify/PassToken/${this.props.match.params.token}`, {
      method: "GET",
      headers,
    })
      .then(async (res) => {
        const val = await res.json();
        console.log(val);
        if (res.ok) {
          setTimeout(() => {
            this.setState({ status: "", loading: false });
          }, 500);
          return;
        }
        setTimeout(() => {
          this.setState({ status: "TokenNotValid", loading: false });
        }, 500);
      })
      .catch((e) => {
        setTimeout(() => {
          this.setState({ status: "Internal", loading: false });
        }, 500);
      });
  }

  handleChangePass = () => {
    this.setState({ loading: false });
    const headers = new Headers();
    headers.append("content-type", "application/json");
    fetch(
      `${url}/setPass/${this.props.match.params.token}/${this.state.password}`,
      {
        method: "PATCH",
        headers,
      }
    )
      .then((res) => {
        if (res.ok) {
          this.setState({ loading: false, status: "Success" });
          return setTimeout(() => {
            this.props.history.push("/login");
          }, 500);
        }
        return this.setState({ loading: false, status: "TokenNotValid" });
      })
      .catch((e) => {
        return this.setState({ loading: false, status: "Internal" });
      });
  };

  render() {
    const { loading, status, password, repass } = this.state;
    return (
      <div
        style={{
          display: "flex",
          justifyContent: "center",
          alignItems: "center",

          height: "100vh",
          background: "rgba(0,0,0,0.9)",
        }}
      >
        <Particles
          style={{ position: "fixed", zIndex: 1, left: 0, top: 0 }}
          params={{
            particles: {
              number: {
                value: 10,
              },
              size: { value: 10 },
            },
          }}
        ></Particles>

        <div
          style={{
            display: "flex",
            zIndex: 5,
            justifyContent: "center",
            alignItems: "center",
            flexDirection: "column",
            position: "relative",
            padding: 10,
            width: loading ? 200 : 300,
            height: loading ? 200 : 500,
            borderRadius: loading ? "50%" : "2%",
            background: "black",
            transition: "all 0.5s ease-in-out",
            boxShadow: "0px 0px 10px rgba(255,255,255,0.2)",
          }}
          className={loading ? "rotate" : ""}
        >
          <Image
            style={{
              position: "absolute",
              top: 10,
              width: loading ? 40 : 80,
              transition: "all 0.5s ease-in-out",
            }}
            src={require("../../../images/complainify.png")}
          ></Image>
          {loading ? (
            <CircularProgress
              style={{ height: 50, width: 50, color: "white" }}
            ></CircularProgress>
          ) : (
            ""
          )}
          {status === "" && !loading ? (
            <Fields
              onChange={(e) => {
                this.setState({ [e.target.name]: e.target.value });
              }}
              password={password}
              repass={repass}
            ></Fields>
          ) : (
            ""
          )}
          {status === "" && !loading ? (
            <Button
              fullWidth
              disabled={
                password.length > 7 &&
                password.length < 128 &&
                password === repass
                  ? false
                  : true
              }
              style={{
                position: "absolute",
                background:
                  password.length > 7 &&
                  password.length < 128 &&
                  password === repass
                    ? "rgba(255,0,0,0.7)"
                    : "rgba(255,0,0,0.2)",
                color: "white",
                padding: 10,
                width: 280,
                bottom: 30,
              }}
              variant="contained"
              onClick={() => {
                this.handleChangePass();
              }}
            >
              Change Password
            </Button>
          ) : (
            ""
          )}
          {status === "TokenNotValid" && !loading ? (
            <NotValidToken></NotValidToken>
          ) : (
            ""
          )}
          {status === "Internal" && !loading ? (
            <InternalError></InternalError>
          ) : (
            ""
          )}
          {status === "Success" && !loading ? <Success></Success> : ""}
        </div>
      </div>
    );
  }
}

const NotValidToken = (props) => {
  return (
    <Fragment>
      <Result
        style={{ marginTop: 70 }}
        status="403"
        title="Session Has Expired"
      ></Result>
      <div
        style={{
          color: "rgba(255,255,255,0.9)",
          fontFamily: "poppins",
          fontWeight: "bold",
          fontSize: 14,
          position: "absolute",
          bottom: 10,
        }}
      >
        Session Has Expired
      </div>
    </Fragment>
  );
};

const Fields = ({ onChange, password = "", repass = "" }) => {
  return (
    <Fragment>
      <input
        name="password"
        className="enterEmail"
        style={{ width: 280, marginBottom: 10 }}
        placeholder="New Password"
        onChange={(e) => {
          onChange(e);
        }}
        type="password"
        value={password}
      ></input>
      <input
        className="enterEmail"
        name="repass"
        style={{ width: 280 }}
        placeholder="Re-Enter Password"
        onChange={(e) => {
          onChange(e);
        }}
        value={repass}
        type="password"
      ></input>
    </Fragment>
  );
};
const InternalError = () => {
  return (
    <Fragment>
      <Result style={{ marginTop: 60 }} status="500"></Result>
      <div
        style={{
          color: "rgba(255,255,255,0.9)",
          fontFamily: "poppins",
          fontWeight: "bold",
          fontSize: 14,
          position: "absolute",
          bottom: 10,
        }}
      >
        Internal Server Error
      </div>
    </Fragment>
  );
};

const Success = () => {
  return (
    <Fragment>
      <Result style={{ marginTop: 60 }} status="success"></Result>
      <div
        style={{
          color: "rgba(255,255,255,0.9)",
          fontFamily: "poppins",
          fontWeight: "bold",
          fontSize: 14,
          position: "absolute",
          bottom: 20,
        }}
      >
        Successfully Changed The Password
      </div>
    </Fragment>
  );
};

export default RP;
